@font-face {
  font-family: 'Montserrat';
  font-display: swap;
  src: url('../fonts/Montserrat-VariableFont_wght.woff2') format('woff2'), url('../fonts/Montserrat-VariableFont_wght.woff') format('woff');
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: var(--basci-font);
}
.container {
  max-width: 1200px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.body {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-color: var(--bg);
}
.body--scrolloff {
  overflow: hidden;
}
:root {
  --basci-font: 'Montserrat', 'Arial', sans-serif;
  --bg: #f7f7f7;
  --white-text-color: #ffffff;
  --main-text-color: #141414;
  --main-color: #1953cf;
  --standart-side-padding: 20px;
  --standart-section-top-padding: 120px;
  --section-title-first-child-size: 50px;
  --section-title-first-child-weight: 500;
  --section-tablet-title-first-child-size: 40px;
  --section-tablet-title-first-child-weight: 500;
  --section-mobile-title-first-child-size: 30px;
  --section-mobile-title-first-child-weight: 500;
  --section-title-second-child-size: 40px;
  --section-title-second-child-weight: 400;
  --section-tablet-title-second-child-size: 30px;
  --section-tablet-title-second-child-weight: 400;
  --section-mobile-title-second-child-size: 20px;
  --section-mobile-title-second-child-weight: 400;
  --standart-font-size: 18px;
  --standart-font-weight: 400;
  --subtext-font-size: 16px;
  --subtext-font-weight: 400;
}
.section-title {
  font-family: var(--basci-font);
  font-size: var(--section-title-first-child-size);
  font-weight: var(--section-title-first-child-weight);
  color: var(--main-text-color);
  white-space: nowrap;
}
@media (max-width: 900px) {
  .section-title {
    font-size: var(--section-tablet-title-first-child-size);
    font-weight: var(--section-tablet-title-first-child-weight);
  }
}
@media (max-width: 600px) {
  .section-title {
    font-size: var(--section-mobile-title-first-child-size);
    font-weight: var(--section-mobile-title-first-child-weight);
  }
}
.section-double-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  font-family: var(--basci-font);
  color: var(--main-text-color);
}
.section-double-title > span:first-child {
  font-size: var(--section-title-first-child-size);
  font-weight: var(--section-title-first-child-weight);
}
@media (max-width: 900px) {
  .section-double-title > span:first-child {
    font-size: var(--section-tablet-title-first-child-size);
    font-weight: var(--section-tablet-title-first-child-weight);
  }
}
@media (max-width: 600px) {
  .section-double-title > span:first-child {
    font-size: var(--section-mobile-title-first-child-size);
    font-weight: var(--section-mobile-title-first-child-weight);
  }
}
.section-double-title > span:last-child {
  font-size: var(--section-title-second-child-size);
  font-weight: var(--section-title-second-child-weight);
}
@media (max-width: 900px) {
  .section-double-title > span:last-child {
    font-size: var(--section-tablet-title-second-child-size);
    font-weight: var(--section-tablet-title-second-child-weight);
  }
}
@media (max-width: 600px) {
  .section-double-title > span:last-child {
    font-size: var(--section-mobile-title-second-child-size);
    font-weight: var(--section-mobile-title-second-child-weight);
  }
}
.general-text {
  font-family: var(--basci-font);
  font-size: var(--standart-font-size);
  font-weight: var(--standart-font-weight);
  color: var(--main-text-color);
}
.banner {
  width: 100%;
  height: 100%;
  min-height: 150vh;
  padding: var(--standart-side-padding);
  background-image: url('../images/banner.jpg');
  background-position: center;
  object-fit: cover;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--bg);
}
.banner__wrap {
  width: 100%;
  height: 100%;
  min-height: 150vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.banner__title {
  position: relative;
  grid-column-start: 2;
  grid-column-end: -1;
  color: var(--white-text-color);
  justify-self: left;
  align-self: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.banner__title::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 100%;
  height: 2px;
  background-color: var(--white-text-color);
}
.banner__text {
  grid-column-start: 1;
  grid-column-end: -1;
  grid-row-start: 2;
  grid-row-end: -1;
  font-family: var(--basci-font);
  font-size: 45px;
  font-weight: 500;
  color: var(--white-text-color);
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.8);
  justify-self: left;
  align-self: center;
}
.about {
  width: 100%;
  height: 100%;
  padding: var(--standart-section-top-padding) 0;
  background-color: var(--bg);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, min-content);
  row-gap: 80px;
}
@media (max-width: 900px) {
  .about {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 40px;
  }
}
.about__title {
  grid-column-start: 1;
  grid-column-end: -1;
  align-self: center;
  padding: 0 var(--standart-side-padding);
}
.about__info {
  grid-row-start: 2;
  grid-row-end: -1;
  align-self: center;
  padding: 0 var(--standart-side-padding);
}
.about__image {
  width: 100%;
  height: max-content;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: -1;
  align-self: center;
  justify-self: strt;
  object-fit: cover;
}
.layout {
  width: 100%;
  height: 100%;
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: min-content min-content 1fr;
  grid-template-areas: 'title data data' 'info data data' 'images images images';
  gap: 20px;
  background-color: var(--bg);
}
@media (max-width: 900px) {
  .layout {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, min-content);
    grid-template-areas: 'title' 'data' 'info' 'images';
  }
}
.layout__title {
  grid-area: title;
  align-self: start;
  text-align: start;
}
.layout__info {
  grid-area: info;
}
.layout__data {
  grid-area: data;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px;
}
.layout__data-item {
  position: relative;
  padding-left: 40px;
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  align-items: center;
  gap: 40px;
}
@media (max-width: 500px) {
  .layout__data-item {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-left: 0;
  }
}
.layout__data-item::before {
  content: '';
  position: absolute;
  height: 2px;
  background-color: var(--main-color);
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: 500px) {
  .layout__data-item::before {
    display: none;
  }
}
.layout__data-item:first-child > .layout__data-title {
  grid-column: 2/3;
}
.layout__data-item:first-child::before {
  grid-column: 3/-1;
  width: 100%;
  right: 0;
}
.layout__data-item:nth-child(2) > .layout__data-title {
  grid-column: 3/3;
}
.layout__data-item:nth-child(2)::before {
  grid-column: 1/2;
  width: 100%;
  right: 0;
}
.layout__data-item:last-child > .layout__data-title {
  grid-column: 1/2;
}
.layout__data-item:last-child::before {
  grid-column: 2/-1;
  width: 100%;
  right: 0;
}
.layout__data-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: var(--basci-font);
  font-size: 30px;
  font-weight: 500;
  color: var(--main-text-color);
}
.layout__data-title > span {
  display: flex;
  font-family: var(--basci-font);
  font-size: 70px;
  font-weight: 500;
  color: var(--main-color);
  white-space: nowrap;
}
@media (max-width: 600px) {
  .layout__data-title {
    font-size: 20px;
  }
  .layout__data-title > span {
    font-size: 50px;
  }
}
.layout__images {
  grid-area: images;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 900px) {
  .layout__images {
    grid-template-columns: 1fr;
  }
}
.layout__image {
  object-fit: cover;
  width: 100%;
}
.parallax {
  min-height: 500px;
  object-fit: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.parallax--one {
  background-image: url('../images/banner.jpg');
}
.parallax--two {
  background-image: url('../images/parallax-2.jpg');
}
@media (max-width: 600px) {
  .parallax {
    min-height: 300px;
  }
}
.inside {
  width: 100%;
  padding: var(--standart-side-padding);
  padding-bottom: var(--standart-section-top-padding);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  gap: 40px;
  background-color: var(--bg);
}
.indise__info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}
@media (max-width: 900px) {
  .indise__info {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}
.inside__description {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: left;
}
.inside__fogure {
  width: 100%;
}
.inside__figure-image {
  width: 100%;
  object-fit: contain;
}
.gallery {
  width: 100%;
  padding: var(--standart-section-top-padding) 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  gap: 30px;
  background-color: var(--bg);
}
.gallery__title {
  padding: 0 var(--standart-side-padding);
}
.gallery__wrap {
  padding: var(--standart-side-padding);
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  gap: 20px;
  justify-content: center;
}
@media (max-width: 900px) {
  .gallery__wrap {
    grid-template-columns: repeat(auto-fit, 250px);
  }
}
@media (max-width: 600px) {
  .gallery__wrap {
    grid-template-columns: repeat(auto-fit, 200px);
  }
}
.gallery__link {
  aspect-ratio: 1/1;
  overflow: hidden;
}
.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: 0.4s all ease-out;
  transform: scale(0.8);
  cursor: pointer;
}
.gallery__image:hover {
  scale: 1.1;
  transition: 0.2s;
}
.gallery__image--show {
  top: 0;
  left: 0;
  opacity: 1;
  transform: scale(1);
}
.location {
  width: 100%;
  padding: var(--standart-section-top-padding) 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  gap: 40px;
  background-color: var(--bg);
}
.location__title {
  padding: 0 var(--standart-side-padding);
}
.location__image {
  width: 100%;
  height: 500px;
  object-fit: cover;
}
.your-house {
  width: 100%;
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content 1fr;
  gap: 40px;
}
@media (max-width: 900px) {
  .your-house {
    grid-template-columns: 1fr;
  }
  .your-house > .your-house__link {
    justify-self: flex-start;
  }
}
.your-house__link {
  position: relative;
  align-self: center;
  justify-self: center;
  padding: 5px 0;
  font-family: var(--basci-font);
  font-size: 20px;
  font-weight: 400;
  color: var(--main-text-color);
  text-decoration: none;
  transition: all ease 0.5s;
}
.your-house__link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 71%;
  height: 2px;
  background-color: var(--main-text-color);
  transition: all ease 0.5s;
}
.your-house__link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: -20%;
  width: 71%;
  height: 2px;
  background-color: var(--main-text-color);
  transition: all ease 0.5s;
}
.your-house__link:hover {
  color: var(--main-color);
}
.your-house__link:hover::before {
  width: 0%;
}
.your-house__link:hover::after {
  width: 0%;
  left: 50%;
}
.your-house__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  grid-column: 1/-1;
}
.about-us {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, min-content);
  gap: 30px;
  padding: var(--standart-side-padding);
}
@media (max-width: 900px) {
  .about-us {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 20px;
  }
}
.about-us__title {
  grid-column: 1/2;
  grid-row: 1/2;
  text-align: left;
}
.about-us__link {
  grid-column: 1/2;
  grid-row: 2/3;
  width: min-content;
  height: min-content;
}
.about-us__logo {
  width: 200px;
  height: 50px;
  object-fit: cover;
}
.about-us__info {
  grid-row: 3/-1;
  font-family: var(--basci-font);
  font-size: var(--subtext-font-size);
  font-weight: var(--subtext-font-weight);
  color: var(--main-text-color);
}
.about-us__info:first-child {
  grid-column: 1/2;
}
.about-us__info:last-child {
  grid-column: 2/-1;
}
.contact {
  width: 100%;
  padding: var(--standart-section-top-padding) var(--standart-side-padding);
  display: grid;
  grid-template-rows: min-content 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.contact__info {
  grid-column: 1/2;
  grid-row: 2/3;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 10px;
}
.contact__address {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 5px;
}
.contact__address > span:first-child {
  font-family: var(--basci-font);
  font-size: 20px;
  font-weight: 500;
  color: var(--main-text-color);
}
.contact__address > span:last-child {
  font-family: var(--basci-font);
  font-size: var(--standart-font-size);
  font-weight: var(--standart-font-weight);
  color: var(--main-text-color);
}
.contact__link {
  font-family: var(--basci-font);
  font-size: var(--standart-font-size);
  font-weight: var(--standart-font-weight);
  color: var(--main-color);
  text-decoration: none;
}
.contact__link:hover,
.contact__link:focus {
  text-decoration: underline;
}
